import React from "react";
import kadi from "../images/avatar-kady.jpg";
import Aiysha from "../images/avatar-aiysha.jpg";
import Arthur from "../images/avatar-arthur.jpg";
import quotes from "../images/icon-quotes.svg";

export function Stories() {
  const storiesList = [
    {
      name: "Kady Baker",
      title: "Product Manager at Bookmark",
      txt: "The team perfectly fit the specialized skill set required. They focused on the most essential features helping us launch the platform eight months faster than planned.",
      img: kadi,
    },
    {
      name: "Aiysha Reese",
      title: "Founder of Manage",
      txt: "We needed to automate our entire onboarding process. The team came in and built out the whole journey. Since going live, user retention has gone through the roof!",
      img: Aiysha,
    },
    {
      name: "Arthur Clarke",
      title: "Co-founder of MyPhysio",
      txt: "Amazing. Our team helped us build an app that delivered a new experience for hiring a physio. The launch was an instant success with 100k downloads in the first month.",
      img: Arthur,
    },
  ];
  return (
      <div className="main-container">
    <div className="app-stories">
      <h1>
        Delivering real results for top companies. Some of our{" "}
        <span>success stories.</span>{" "}
      </h1>

      <div className="stories-cards">
        {storiesList.map((story) => (
          <div className="stories-card">
              <div className="quotes"><img className="quotes-img" src={quotes}/></div>
            <div className="stories-txt">
              <p>{story.txt}</p>
            </div>
            <h4>{story.name}</h4>
            <h6>{story.title}</h6>
            <img className="person-img" src={story.img} />
          </div>
        ))}
      </div>
    </div></div>
  );
}
